<template>
  <div class="container">
    <order v-for="(cardList, o, oindex) in orders" :key="`order-${oindex}`" :cards="cardList" />
    <pick @pick="pickCard" />

    <div class="flow" v-if="showFlows">
      <ul>
        <li :class="[index >= sequence - 3 && index < sequence ? 'highlight' : '']" v-for="(card, index) in cardList" :key="`card-${card.card}-${card.number}`">
          {{ index }}. {{ card.card }} - {{ card.number }}
        </li>
      </ul>
    </div>

    <div class="openedCards" v-if="showFlows">
      <ul>
        <li v-for="(card, index) in openedCards" :key="`card-${card.card}-${card.number}`">
          {{ index }}. {{ card.card }} - {{ card.number }}
        </li>
      </ul>
    </div>

    <div class="new-button">
      <input type="checkbox" v-model="showFlows" id="showFlows"><label for="showFlows">Show Flows</label><br>
      <button @click="newAndBlend">New & Blend</button>
    </div>
  </div>
</template>

<script>
import Order from "@/components/Order";
import Pick from "@/components/Pick";
import { computed, onMounted, ref } from 'vue';
import { useStore } from 'vuex';


export default {
  name: "Play",
  components: {Pick, Order },
  setup() {
    const store = useStore()
    const orders = computed(() => store.getters.cards.orders)
    const cardList = computed(() => store.getters.cards.cardList)
    const openedCards = computed(() => store.getters.cards.openedCards)
    const sequence = computed(() => store.getters.cards.sequence);
    const showFlows = ref(false);

    onMounted(() => {
      store.commit('newAndBlend')
    })

    const pickCard = (card) => {
      store.dispatch('pickCard', card)
    }

    const newAndBlend = () => {
      store.commit('newAndBlend')
    }

    return {
      orders,
      cardList,
      sequence,
      openedCards,
      showFlows,
      pickCard,
      newAndBlend,
    }
  },
}
</script>

