import { createStore } from "vuex";

export default createStore({
    state: {
        cardTypes: ['clubs', 'diamonds', 'hearts', 'spades'],
        allCards: [],
        openedCards: [],
        cardList: [],
        orders: {0: [], 1: [], 2: [], 3: [], 4: [], 5: [], 6: [], 7: []},
        sequence: 0,
        range: '',
    },
    mutations: {
        clearOpenedCards(state) {
            while (state.openedCards.length > 0) {
                state.openedCards.pop()
            }
        },
        clearCardList(state) {
            while (state.cardList.length > 0) {
                state.cardList.pop()
            }
        },
        clearAllCards(state) {
            while (state.allCards.length > 0) {
                state.allCards.pop()
            }
        },
        popLastCard(state) {
            const cardIndex = state.openedCards.length - 1;
            state.openedCards.splice(cardIndex, 1);
            state.cardList.splice(cardIndex, 1);
            state.sequence -= 1
        },
        clearOrders(state) {
            for (const orderList of Object.values(state.orders)) {
                while (orderList.length > 0) {
                    orderList.pop()
                }
            }
        },
        generateAllCards(state) {
            for (let i = 0; i < 4; i++) {
                for (let j = 1; j < 14; j++) {
                    state.allCards.push({card: state.cardTypes[i], number: j});
                }
            }
        },
        blendCards(state) {
            const cardCount = state.allCards.length;

            for (let i = 0; i < cardCount; i++) {
                const cardIndex = Math.floor(Math.random() * state.allCards.length);
                state.cardList.push(state.allCards[cardIndex]);
                state.allCards.splice(cardIndex, 1);
            }
        },
        newAndBlend(state) {
            this.commit('clearOpenedCards')
            this.commit('clearCardList')
            this.commit('clearAllCards')
            this.commit('clearOrders')
            this.commit('generateAllCards')
            this.commit('blendCards')
            state.sequence = 0
        }
    },
    actions: {
        openCard({ state, commit }) {
            if (state.sequence === 0) {
                commit('clearOpenedCards')
            }
            const end = state.sequence + 3 > state.cardList.length ? state.cardList.length : state.sequence + 3;

            state.range = `${state.sequence} - ${end}`
            for (let i = state.sequence; i < end; i++) {
                state.openedCards.push(state.cardList[i])
                state.sequence += 1
            }

            if (end >= state.cardList.length) {
                state.sequence = 0;
            }
        },
        pickCard({ state, commit }, card) {
            if (card.number === 1 || card.number === 13) {
                for (const value of Object.values(state.orders)) {
                    if (value.length === 0) {
                        value.push(card)
                        commit('popLastCard');
                        return;
                    }
                }
            } else {
                for (const value of Object.values(state.orders)) {
                    if (value.length === 0) {
                        return;
                    }
                    const lastCard = value[value.length - 1];
                    if (lastCard.card === card.card && (lastCard.number - 1 === card.number || lastCard.number + 1 === card.number)) {
                        value.push(card);
                        commit('popLastCard');
                        return;
                    }
                }
            }
        },
    },
    getters: {
        cards: state => state,
        lastCard: (state) => {
            if (state.openedCards.length > 0) {
                return state.openedCards[state.openedCards.length - 1];
            }
            return {card: null, number: null}
        },
    },
})
