<template>
  <div class="pick-area">
    <card
        v-if="lastCard.card && lastCard.number"
        :card="lastCard.card" :number="lastCard.number"
        @click="$emit('pick', lastCard)"
    />
    <img v-else :src="require('@/assets/cards/joker.svg')" alt="joker" />
    <img @click="openCard" :src="require('@/assets/cards/joker.svg')" alt="joker" />
  </div>
</template>

<script>
import Card from "@/components/Card";
import { useStore } from 'vuex';
import { computed } from "vue";

export default {
  name: "Pick",
  components: { Card },
  props: {},
  emits: ['pick'],
  setup() {
    const store = useStore();
    const lastCard = computed(() => store.getters.lastCard)

    const openCard = () => {
      store.dispatch('openCard')
    }

    return {
      openCard,
      lastCard,
    }
  },
}
</script>

