<template>
  <div class="order">
    <card :card="card.card" :number="card.number" v-if="card.card && card.number" />
  </div>
</template>

<script>
import Card from "@/components/Card";
import { computed } from "vue";

export default {
  name: "Order",
  components: { Card },
  props: {
    cards: {
      type: Array,
      default: () => []
    }
  },
  setup(props) {
    const card = computed(() => {
      if (props.cards.length > 0) {
        return props.cards[props.cards.length - 1];
      }
      return {card: null, number: null}
    })

    return {
      card,
    }
  },
}
</script>

